
header {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 3rem;
  margin: 0;
  padding: 0;

  background-color: $gray;
}

ul {
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover li a{
    filter: blur(0.07rem);
  }

  &:hover li a:hover {
    font-size: 1.5rem;
    filter: blur(0rem);
  }
}

li {
  display: block;
  padding: 1.2rem;
  color: #fff;
  font-size: 1rem;
  font-family: fantasy;
  text-transform: uppercase;

  & a {
    font-family: Caveat, sans-serif;
    text-decoration: none;
    color: #fff;
    transition: .6s;
  }
}





